body {
  color: #283244;
  font-family: "Noto Sans", sans-serif;
  font-size: 0.875rem;
  line-height: 1.7em;
}
@media screen and (min-width: 992px) {
  body {
    font-size: 1rem;
  }
}

#wrapper {
  position: relative;
  overflow: hidden;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
}

.container {
  max-width: 1140px;
  padding: 0 20px;
  margin: 0 auto;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  transition: all 0.5s;
}
.header.active {
  position: fixed;
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: 0px 10px 50px -22px rgba(199, 199, 199, 0.8);
  animation: animation-header 0.3s;
}
.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
}
.header__logo {
  position: relative;
  width: 40px;
  z-index: 11;
}
.header__nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 60px 0 80px;
  background-color: #fff;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s;
}
@media screen and (min-width: 992px) {
  .header__nav {
    position: static;
    display: flex;
    align-items: center;
    width: auto;
    height: auto;
    padding: 0;
    background-color: transparent;
    opacity: 1;
    visibility: visible;
  }
}
.header__nav.active {
  opacity: 1;
  visibility: visible;
}
.header__menu-item {
  position: relative;
  display: block;
  border-bottom: 1px solid #5e66d2;
}
@media screen and (min-width: 992px) {
  .header__menu-item {
    display: inline-block;
    margin-left: 20px;
    border-bottom: none;
  }
}
.header__menu-link {
  display: block;
  padding: 12px 20px;
  background-color: #575fcc;
  color: #fff;
  font-family: "Heebo", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5em;
  letter-spacing: 0.08em;
  transition: all 0.5s;
}
.header__menu-sub {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 300px;
  opacity: 0;
  visibility: hidden;
}
.header__menu-sub .header__menu-item {
  display: block;
}
.header__contact {
  margin: 40px 0 0;
  padding: 0 20px;
}
@media screen and (min-width: 992px) {
  .header__contact {
    display: none;
  }
}
@media screen and (min-width: 1200px) {
  .header__contact {
    display: flex;
    align-items: center;
    margin: 0 0 0 30px;
    padding: 0;
  }
}
.header__contact-tel {
  max-width: 300px;
  margin: 0 auto 10px;
}
@media screen and (min-width: 992px) {
  .header__contact-tel {
    margin: 0;
  }
}
.header__contact-tel-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 25px;
  background-color: #ff6d3b;
  border-radius: 10px;
  color: #fff;
  font-family: "Heebo", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 1.5em;
  letter-spacing: 0.05em;
}
@media screen and (min-width: 992px) {
  .header__contact-tel-link {
    padding: 0;
    background-color: transparent;
    color: #283244;
  }
}
.header__contact-tel-link::before {
  content: "";
  display: inline-flex;
  width: 30px;
  height: 30px;
  margin-right: 5px;
  background: url(../images/icon_iphone_white.svg) no-repeat center/contain;
}
@media screen and (min-width: 992px) {
  .header__contact-tel-link::before {
    background-image: url(../images/icon_iphone_black.svg);
  }
}
.header__contact-mail {
  max-width: 300px;
  margin: 0 auto;
}
@media screen and (min-width: 992px) {
  .header__contact-mail {
    margin: 0 0 0 20px;
  }
}
.header__contact-mail-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 25px;
  background-color: #ff6d3b;
  border-radius: 10px;
  color: #fff;
  font-family: "Heebo", sans-serif;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 1.5em;
  letter-spacing: 0.05em;
  transition: all 0.5s;
}
@media screen and (min-width: 992px) {
  .header__contact-mail-link {
    border-radius: 70px;
  }
  .header__contact-mail-link:hover {
    color: #fff;
    background-color: #283244;
  }
  .header__contact-mail-link:hover::before {
    background: url(../images/icon_mail_o.svg) no-repeat center/contain;
  }
}
.header__contact-mail-link::before {
  content: "";
  display: inline-flex;
  width: 30px;
  height: 30px;
  margin-right: 5px;
  background: url(../images/icon_mail.svg) no-repeat center/contain;
  transition: all 0.5s;
}

.hamburger {
  position: relative;
  width: 35px;
  height: 22px;
  z-index: 11;
  transition: all 0.5s;
  cursor: pointer;
}
@media screen and (min-width: 992px) {
  .hamburger {
    display: none;
  }
}
.hamburger::before, .hamburger::after,
.hamburger span::before,
.hamburger span::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #283244;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transition: all 0.5s;
}
.hamburger::before {
  top: 0;
}
.hamburger::after {
  bottom: 0;
}
.hamburger span::before,
.hamburger span::after {
  top: 10px;
}
.hamburger.active::before, .hamburger.active::after {
  opacity: 0;
}
.hamburger.active span::before {
  transform: translateX(-50%) rotate(40deg);
}
.hamburger.active span::after {
  transform: translateX(-50%) rotate(-40deg);
}

.footer {
  padding: 0;
  background-color: #283244;
}
.footer__map {
  position: relative;
  width: 100%;
  height: 270px;
  overflow: hidden;
}
@media screen and (min-width: 992px) {
  .footer__map {
    height: 450px;
  }
}
.footer__map iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 160%;
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
}
.footer__map::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.footer__content {
  padding: 50px 0;
}
@media screen and (min-width: 768px) {
  .footer__content {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
}
@media screen and (min-width: 992px) {
  .footer__content {
    padding: 100px 0;
  }
}
.footer__logo {
  width: 230px;
  margin-bottom: 30px;
}
.footer__desc {
  color: #c7c7c7;
}
.footer__address {
  margin-bottom: 10px;
}
.footer__tel {
  margin-bottom: 10px;
}
.footer__tel span {
  font-size: 1.25rem;
  font-weight: 400;
}
.footer__time span {
  font-size: 1.25rem;
  font-weight: 400;
}
.footer__nav-wrap {
  display: none;
}
@media screen and (min-width: 768px) {
  .footer__nav-wrap {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-shrink: 0;
    margin-left: 40px;
    padding-top: 30px;
  }
}
@media screen and (min-width: 992px) {
  .footer__nav-wrap {
    flex-wrap: nowrap;
  }
}
.footer__nav-col {
  width: 270px;
}
@media screen and (min-width: 992px) {
  .footer__nav-col {
    width: 300px;
  }
}
.footer__nav-col:first-child {
  width: 150px;
  margin-right: 20px;
}
@media screen and (min-width: 992px) {
  .footer__nav-col:first-child {
    width: 200px;
    margin-right: 100px;
  }
}
.footer__nav-title {
  margin-bottom: 20px;
  padding-bottom: 2px;
  border-bottom: 1px solid #c7c7c7;
  color: #c7c7c7;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5em;
}
@media screen and (min-width: 992px) {
  .footer__nav-title {
    font-size: 1.125rem;
  }
}
.footer__nav-item {
  margin-bottom: 7px;
}
.footer__nav-item:last-child {
  margin-bottom: 0;
}
.footer__nav-link {
  position: relative;
  padding-left: 20px;
  color: #c7c7c7;
  font-size: 0.875rem;
  line-height: 1.5em;
  transition: all 0.5s;
}
@media screen and (min-width: 992px) {
  .footer__nav-link {
    font-size: 1rem;
  }
}
.footer__nav-link::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 0;
  width: 10px;
  height: 1px;
  background-color: #fff;
}
.footer__nav-link:hover {
  color: #fff;
}
.footer__copy {
  padding: 15px;
  background-color: #283244;
  border-top: 1px solid #363d4b;
  color: #c7c7c7;
  font-size: 0.75rem;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .footer__copy {
    font-size: 0.875rem;
  }
}
.footer__social {
  margin-top: 20px;
}
.footer__social svg {
  width: 30px;
  height: auto;
}
.footer__social g {
  transition: all 0.5s;
}
.footer__social a {
  display: inline-block;
  margin-right: 10px;
}
.footer__social a:last-child {
  margin-right: 0;
}
@media screen and (min-width: 768px) {
  .footer__social a:hover svg g {
    fill: #fff;
  }
}

.totop {
  position: fixed;
  bottom: 70px;
  right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: #ff6d3b;
  border-radius: 5px;
  cursor: pointer;
  z-index: 9;
  transition: all 0.5s;
}
@media screen and (min-width: 992px) {
  .totop {
    bottom: 100px;
    width: 50px;
    height: 50px;
  }
}
.totop::before {
  content: "";
  position: relative;
  transform: rotate(-90deg);
  display: inline-block;
  width: 25px;
  height: 25px;
  background: url(../images/icon_arrow.svg) no-repeat center/contain;
}
@media screen and (min-width: 768px) {
  .totop::before {
    width: 35px;
    height: 35px;
  }
}
@media screen and (min-width: 992px) {
  .totop:hover {
    opacity: 0.7;
  }
}

.heading {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 40px;
  color: #283244;
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 1.5em;
}
@media screen and (min-width: 992px) {
  .heading {
    width: 400px;
    margin-bottom: 0;
    font-size: 1.875rem;
  }
}
.heading__en {
  position: relative;
  margin-bottom: 25px;
  color: #575fcc;
  font-family: "Heebo", sans-serif;
  font-size: 3.125rem;
  font-weight: 400;
}
@media screen and (min-width: 992px) {
  .heading__en {
    font-size: 3.75rem;
  }
}
.heading__en::before {
  content: "";
  position: absolute;
  bottom: -16px;
  left: 0;
  width: 50px;
  height: 2px;
  background-color: #ff6d3b;
}
.heading__wrap {
  margin-bottom: 50px;
}
@media screen and (min-width: 992px) {
  .heading__wrap {
    display: flex;
    justify-content: space-between;
    margin-bottom: 70px;
  }
}
@media screen and (min-width: 992px) {
  .heading__content {
    width: calc(100% - 400px);
  }
}
.heading__title {
  margin-bottom: 20px;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.7em;
}
@media screen and (min-width: 992px) {
  .heading__title {
    margin-bottom: 30px;
    font-size: 1.5rem;
  }
}
.heading--white {
  color: #fff;
}
.heading--white .heading__en {
  color: #fff;
}
.heading--sm {
  font-size: 1rem;
}
@media screen and (min-width: 992px) {
  .heading--sm {
    font-size: 1.125rem;
  }
}
.heading--sm .heading__en {
  font-size: 2.5rem;
}
@media screen and (min-width: 992px) {
  .heading--sm .heading__en {
    font-size: 3.125rem;
  }
}
@media screen and (min-width: 992px) {
  .heading--row {
    flex-direction: row;
    width: auto;
  }
}
@media screen and (min-width: 992px) {
  .heading--row .heading__en {
    margin-right: 30px;
  }
}

.button {
  max-width: 280px;
  width: 100%;
  margin: 0 auto;
  border-radius: 100px;
  overflow: hidden;
}
.button__link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  padding: 17px 10px;
  background-color: #283244;
  color: #fff;
  font-size: 1.125rem;
  font-weight: 400;
  text-align: center;
  line-height: 1.5em;
  letter-spacing: 0.05em;
}
@media screen and (min-width: 768px) {
  .button__link {
    padding: 20px;
    font-size: 1.25rem;
  }
